module.exports = "<h3 class=main-header> <input type=text class=title v-model=questionnaire.title placeholder=编辑问卷标题></h3> <div class=main-body> <div class=question-topic v-for=\"question in questionnaire.content\"> <b class=question-num>{{\"Q\"+($index+1)}}</b> <div class=question-content> <input type=text v-model=question.title placeholder=编辑题目> <div v-if=\"question.type==='textarea'\"> <textarea></textarea> <div class=question-required> <input type=checkbox v-model=question.required> <b>此题是否必填</b> </div> </div> <ul class=question-options v-else> <li v-for=\"option in question.options\"> <input :type=question.type disabled=disabled> <input type=text v-model=option.text placeholder=编辑选项> <b class=delete-option @click=\"deleteOption($index, question)\">×</b> </li> </ul> </div> <ul class=clearfix> <li class=question-operate @click=deleteQuestion($index)>删除</li> <li class=question-operate @click=\"copyQuestion($index, question)\">复用</li> <li class=question-operate @click=\"downQuestion($index, question)\" v-if=\"$index !== questionnaire.content.length-1\">下移</li> <li class=question-operate @click=\"upQuestion($index, question)\" v-if=\"$index!==0\">上移</li> <li class=question-operate @click=addOption(question) v-if=\"question.type !=='textarea'\">添加选项</li> </ul> </div> <div class=add-question> <div v-show=show transition=question-type class=question-type> <button @click=\"addQuestion('radio')\"> <i class=iconfont>&#xe602;</i> 单选题 </button> <button @click=\"addQuestion('checkbox')\"> <i class=iconfont>&#xe601;</i> 多选题 </button> <button @click=\"addQuestion('textarea')\"> <i class=iconfont>&#xe603;</i> 文本题 </button> </div> <div class=add-question-btn @click=addShow> <span>+</span> 添加问题 </div> </div> </div> <div class=main-footer> <div class=float-left> <p class=float-left>问卷截止日期:&nbsp;</p> <calendar :date.sync=questionnaire.endTime></calendar> <strong v-if=timeEarly>!选择日期早于当前日期</strong> </div> <div class=float-right> <a class=operate-btn @click=save v-link=\"'/home'\">保存问卷</a> <a class=operate-btn @click=pending>发布问卷</a> </div> </div> <pop-up :show.sync=popup.show :prompt=popup.prompt :message=popup.msg :callback=popup.call></pop-up>";


